<template>
  <div class="bg" align="center">
    <div class="content" ref="content" align="left">
      <div class="row">
        <div class="col-11">
          <h1>{{data.blog.title}}<small>赞:{{data.blog.good}}</small></h1>
        </div>
        <div class="col-1 ml-auto blogGoodBox" align="right">
          <button class="btn btn-primary" @click="blogGood()">赞</button>
        </div>
      </div>
      <hr />
      <div class="inner" v-html="data.blog.inner">{{data.blog.inner}}</div>
      <hr />
      <div>
        <h3 class="text-primary">我要评论</h3>
        <div class="row">
          <div class="col-8">
            <textarea class="form-control d-block rounded" v-model="plText"></textarea>
          </div>
          <div class="col-4">
            <button class="btn btn-primary" @click="pl">发表</button>
          </div>
        </div>
        <hr />
        <div>
          <div v-for="pl in data.pls" :key="pl" class="pl">
            <div class="row">
              <div class="col-8">
                <div>
                  <h4
                    class="text-primary"
                  >[{{pl.date}}]{{pl.user}}</h4>
                </div>
                {{pl.inner}}
              </div>
              <div class="col-4">
                <div class="row">
                  <div class="col-6">
                    <h4 class="text-primary">
                      <small>赞数:{{pl.goodNum}}</small>
                    </h4>
                  </div>
                  <div class="col-6">
                    <button class="btn btn-primary" v-bind:id="'good'+pl.id" @click="good">赞</button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "axios";
import "../../assets/code.css";
export default {
  data() {
    return {
      data: {
        blog: {
          title: "",
          inner: ""
        }
      },
      plText:""
    };
  },
  methods: {
    getData() {
      Axios.get("/api/page/blog?id=" + this.$route.params.id).then(response => {
        var data = response.data;
        if (data.message == "success") {
          // data.blog.inner=window.filterXSS(data.blog.inner,{
          //   whiteList:{
          //     "br":[],
          //     "a":["href","target","name","id"],
          //     "h1":[],
          //     "h2":[],
          //     "h3":[],
          //     "h4":[],
          //     "h5":[],
          //     "h6":[],
          //     "p":[],
          //     "table":[],
          //     "thead":[],
          //     "tbody":[],
          //     "th":["style"],
          //     "tr":[],
          //     "td":["style"],
          //     "strong":[],
          //     "em":[],
          //     "s":[],
          //     "blockquote":[],
          //     "pre":[],
          //     "div":["class"],
          //     "code":["class"],
          //     "span":["class"],
          //     "img":["src","alt"],
          //     "hr":[],
          //     "ul":[],
          //     "li":[],
          //     "ol":[],
          //     "sup":[],
          //     "mark":[],
          //     "sub":[],
          //     "math":[],
          //     "semantics":[],
          //     "mrow":[],
          //     "mi":[],
          //     "annotation":["application/x-tex"],
          //     "mtext":[],
          //     "mi":["mathvariant"],
          //     "mo":[],
          //     "mn":[]
          //   }
          // });
          data.blog.inner=window.filterXSS(data.blog.inner,{
            onIgnoreTag:(tag,html,options)=>{
              if(tag in {
                "script":1,
                "form":1,
                "input":1,
                "textarea":1,
                "select":1,
                "option":1,
              }){
                return "&lt;"+tag+"&gt";
              }if(tag=="annotation"){
                return ""
              }else{
                return html
              }
            },
            onIgnoreTagAttr(tag,name,value,isWhiteAttr){
              if(tag=="a"&&name=="href"){
                if(value.slice(0,11)=="javascript:"){

                }else{
                  return name+"=\""+value+"\""
                }
              }
            },
            whiteList:{
              a:[]
            }
          })
          this.data = data;
        } else if (data.message == "the id is wrong") {
          this.$router.push("/Error/404");
        }
      });
    },
    pl() {
        Axios.post("/api/blog/pl",{
            blog:this.data.blog.id,
            inner:this.plText
        }).then((response)=>{
            this.getData();
        })
    },
    good(e){
        var id=parseInt(e.target.id.slice(4));
        console.log(e.target.id);
        Axios.post("/api/blog/good",{
            id:id
        }).then(()=>{
            this.getData();
        })
    },
    blogGood(){
      Axios.post("/api/blog/blogGood",{
        id:this.data.blog.id
      }).then(response=>{
        if(response.message=="no signIn"){
          this.$router.push("/user/signIn");
        }else{
          this.getData();
        }
      })
    }
  },
  mounted() {
    this.getData();
  }
};
</script>
<style scoped>
.bg {
  padding-top: 1px;
  width: 100%;
  height: 100%;
  background-color: lemonchiffon;
  overflow: auto;
}
.content {
  display: inline-block;
  margin-top: 100px;
  min-width: 800px;
  max-width:1200px;
  min-height: 600px;
  padding-left: 30px;
  padding-right: 30px;
  margin-left: auto;
  margin-right: auto;
  background-color: beige;
}
.pl {
  background-color: whitesmoke;
  border-radius: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 20px;
  padding-bottom: 10px;
}
.blogGoodBox{
  padding-top:20px;
}
@media screen and (max-width: 800px){
  .content{
    min-width:500px;
    max-width:600px;
  }
  h1{
    font-size:40px !important;
  }
  h2{
    font-size:25px !important;
  }
  h3{
    font-size:20px !important;
  }
  h4{
    font-size:18px !important;
  }
}
@media screen and (max-width: 500px){
  .content{
    min-width:300px;
    max-width:400px;
  }
}
</style>
<style>
.katex-mathml{
  display:none;
}
table{
  width:100%;
}
table>thead>tr{
  background-color: rgb(218, 228, 248);
}
table>tbody>tr{
  border-top: 2px solid skyblue;
  border-bottom: 2px solid skyblue;
  background-color:whitesmoke;
}
div.hljs-left{
  text-align:left;
}
div.hljs-center{
  text-align:center;
}
div.hljs-right{
  text-align:right;
}
mark{
  margin:5px;
  background-color: dodgerblue;
  color:white;
}
blockquote{
  background-color: aliceblue;
  color:darkgray;
  border-left: 10px solid gray;
  padding-left:10px;
}
</style>


